import React, { useState, useEffect } from "react";
import DataService from '../services/data.service'

const Home = () => {
  const [state,setState] = useState({
    content: []
  })

  useEffect(() => {
    DataService.getStockFile().then(
      response => {
        setState({
          content: response.data
        });
      },
      error => {
        setState({
          content:
            (error.response && error.response.data) ||
            error.message ||
            error.toString()
        });
      }
    );
  },[])

    return (
      <div className="container">
        <header className="jumbotron">
          <h3>What's New</h3>
          {state.content.map((item,key) => (
            <div>{item.sku} ({item.ean}) {item.description} - {item.stockQuantity}</div>
          ))}

        </header>
      </div>
    );
}

export default Home