import React, {useState} from 'react'
import AuthService from '../../services/data.service'

const AddInventory = () => {

    const [state,setState] = useState({
        sku: '',
        ean: '',
        description: '',
        stockQuantity: '',
        showStockListing: true
    })

    const handleChange = (e) => {
        setState({...state,
            [e.target.name]: e.target.value
        })
    }

    const handleAddInventory = (e) => {
        e.preventDefault();
    
       // setState({...state,
       //   message: "",
       //   successful: false
       // });
          AuthService.addInventory(
            (state.sku || '').toLowerCase(),
            (state.ean || '').toLowerCase(),
            (state.description || '').toLowerCase(),
            state.stockQuantity,
            state.showStockListing,
          ).then(
            response => {
              setState({
                message: response.data.message,
                successful: true
              });
            },
            error => {
              const resMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
    
              setState({
                successful: false,
                message: resMessage
              });
            }
          );
        
      }

    return(
        <>
        <div>Add Inventory</div>

        <form>
            SKU: <input type="text" name={'sku'} value={state.sku} onChange={handleChange} /><br />
            BarCode: <input type="text" name={'ean'} value={state.ean} onChange={handleChange} /><br />
            Description: <input type="text" name={'description'} value={state.description} onChange={handleChange} /><br />
            Stock Quantity: <input type="text" name={'stockQuantity'} value={state.stockQuantity} onChange={handleChange} /><br />
            <button onClick={handleAddInventory} >Add Stock</button><br />
            Status: {state.successful && 'Line added to Stock File'}<br />
            Status: {state.message}
        </form>
        </>
    )
}

export default AddInventory