import axios from 'axios';
import authHeader from './auth.header';

const API_URL = 'https://disruptersapi.herokuapp.com/';
//const API_URL = 'http://localhost:8080/';

class DataService {
  getStockFile() {
    return axios.get(API_URL + 'api/stockfile', { headers: authHeader() })
  }

  addInventory(sku,ean,description,stockQuantity,showStockListing) {
    return axios.post(API_URL + "api/stockfile/", {
      sku,
      ean,
      description,
      stockQuantity,
      showStockListing
    });
  }

}

export default new DataService();
